<template>
  <div id="app">
    <div id="site-border-left"></div>
    <div id="site-border-right"></div>
    <div id="site-border-top"></div>
    <div id="site-border-bottom"></div>
    <!-- ç¿»è¯‘æ‚¬æµ®æŒ‰é’® -->
    <lang-com v-show="isShow"></lang-com>
    <el-scrollbar style="height:100%" ref="elScrollBar" v-if="!isMobile">
      <router-view></router-view>
    </el-scrollbar>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import TranslationBar from "./components/client/public/TranslateBar";
export default {
  name: "App",
  components: {
    "lang-com": TranslationBar
  },
  data() {
    return {
      isShow: true,
      isMobile: null
    };
  },
  methods: {
    version() {
      var u = navigator.userAgent,
        app = navigator.appVersion;
      return {
        trident: u.indexOf("Trident") > -1, //IEå†…æ ¸
        presto: u.indexOf("Presto") > -1, //operaå†…æ ¸
        webKit: u.indexOf("AppleWebKit") > -1, //è‹¹æžœã€è°·æ­Œå†…æ ¸
        gecko: u.indexOf("Gecko") > -1 && u.indexOf("KHTML") == -1, //ç«ç‹å†…æ ¸
        mobile: !!u.match(/AppleWebKit.*Mobile.*/), //æ˜¯å¦ä¸ºç§»åŠ¨ç»ˆç«¯
        ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //iosç»ˆç«¯
        android: u.indexOf("Android") > -1 || u.indexOf("Linux") > -1, //androidç»ˆç«¯æˆ–è€…ucæµè§ˆå™¨
        iPhone: u.indexOf("iPhone") > -1, //æ˜¯å¦ä¸ºiPhoneæˆ–è€…QQHDæµè§ˆå™¨
        iPad: u.indexOf("iPad") > -1, //æ˜¯å¦iPad
        webApp: u.indexOf("Safari") == -1, //æ˜¯å¦webåº”è¯¥ç¨‹åºï¼Œæ²¡æœ‰å¤´éƒ¨ä¸Žåº•éƒ¨
        weixin: u.indexOf("MicroMessenger") > -1, //æ˜¯å¦å¾®ä¿¡ ï¼ˆ2015-01-22æ–°å¢žï¼‰
        qq: u.match(/\sQQ/i) == " qq" //æ˜¯å¦QQ
      };
    }
  },
  watch: {
    $route: function(nv, ov) {
      if (!nv || nv.name == "login" || nv.path.indexOf("admin") > -1) {
        this.isShow = false;
      } else {
        this.isShow = true;
      }
    }
  },
  beforeCreate() {
    this.$nextTick(_ => {
	  this.isMobile = this.version().mobile;
    });
  }
};
</script>

<style lang="less" scoped>
</style>


