<template>
    <div>
        <el-table
            :data="curChosenArcComment.comment"
            border="true"
            style="width: 100%"
            :cell-class-name="setCell"
        >
            <el-table-column prop="time" label="æ—¥æœŸ" width>
                <template slot-scope="scope">
                    <p>{{scope.row.time|date}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="user" label="ç”¨æˆ·"></el-table-column>
            <el-table-column prop="comment" label="ç•™è¨€å†…å®¹" width="400"></el-table-column>
            <el-table-column label="æ“ä½œ">
                <template slot-scope="scope">
                    <el-button
                        size="small"
                        type="normal"
                        @click="handleCheckComment(scope.$index, scope.row)"
                    >æŸ¥çœ‹</el-button>
                    <el-button
                        size="small"
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)"
                    >åˆ é™¤</el-button>
                </template>
            </el-table-column>
            <el-dialog
                title="è¯„è®ºè¯¦æƒ…"
                id="comment_detail"
                width="30%"
                :visible.sync="innerVisible"
                append-to-body
            >
                <el-form label-width="100px">
                    <el-form-item>
                        <div class="chosen_content" v-html="commentDetail"></div>
                    </el-form-item>
                </el-form>
            </el-dialog>
        </el-table>
    </div>
</template>

<script>
export default {
    data() {
        return {
            innerVisible: false,
            commentDetail: ''
        };
    },
    methods: {
        handleDelete(index, row) {
            this.$confirm('æ°¸ä¹…åˆ é™¤è¯¥è¯„è®º, æ˜¯å¦ç»§ç»­?', 'æç¤º', {
                confirmButtonText: 'ç¡®å®š',
                cancelButtonText: 'å–æ¶ˆ',
                type: 'warning'
            }).then(_ => {
                this.curChosenArcComment.comment.forEach((v, i) => {
                    if (v.time == row.time) {
                        this.curChosenArcComment.comment.splice(i, 1)
                    }
                })
                /* åˆ é™¤è¯„è®ºæ“ä½œ */
                this.$axios({
                    url: '/index/comment/del',
                    method: 'post',
                    data: {
                        contentid: this.curChosenArcComment._id,
                        time: row.time
                    }
                }).then(res => {
                    if (res.data.code == 1) {
                        this.$message({
                            type: 'success',
                            message: "æˆåŠŸåˆ é™¤è¯„è®º !"
                        });
                        this.upDateArc()
                    } else {
                        this.$message({
                            type: 'danger',
                            message: 'åˆ é™¤å¤±è´¥, è¯·è”ç³»ç¨‹åºå‘˜ !'
                        });
                    }
                })
            })
        },
        setCell(row, column, rowIndex, columnIndex) { // è®¾ç½®ç±»åæ¥æ›´æ”¹æ ·å¼
            if (row.column.label === 'ç•™è¨€å†…å®¹') {
                return 'cell_content';
            }
        },
        handleCheckComment(index, row) {
            this.innerVisible = true;
            this.commentDetail = row.comment;
        }
    },
    props: ['curChosenArcComment', 'upDateArc'],
};
</script>

<style lang="less" scoped>
.chosen_content {
    border: 1px solid #e4e7ed;
    background-color: #f5f7fa;
    border-radius: 4px;
    padding: 0 15px;
}
</style>


