<template>
    <div id="category">
        <el-table :data="categoryData" border style="width: 100%" :cell-class-name="setIdColumn">
            <el-table-column prop="_id" label="åˆ†ç±»ID"></el-table-column>
            <el-table-column prop="name" label="åˆ†ç±»åç§°"></el-table-column>
            <el-table-column prop="addtime" label="æ–°å¢žæ—¶é—´">
                <template slot-scope="scope">
                    <p>{{scope.row.addtime|date}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="edittime" label="ä¸Šæ¬¡ä¿®æ”¹">
                <template slot-scope="scope">
                    <p>{{scope.row.edittime|date}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="banner" label="åˆ†ç±»ç¼©ç•¥å›¾">
                <template slot-scope="scope">
                    <el-image
                        style="width: 150px; height: 34px"
                        :src="scope.row.banner"
                        fit="cover"
                    ></el-image>
                </template>
            </el-table-column>
            <el-table-column label="æ“ä½œ">
                <!-- <span>123</span> -->
                <template slot-scope="scope">
                    <el-button size="mini" @click="edit(scope.$index, scope.row)">ç¼–è¾‘</el-button>
                    <el-button size="mini" type="danger" @click="del(scope.$index, scope.row)">åˆ é™¤</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-button type="text" @click='open'>æ–°å¢žåˆ†ç±»</el-button>
        <!-- æ”¹ç”¨dialogç»„ä»¶ -->
        <el-dialog :title="handleType=='add'?'æ·»åŠ åˆ†ç±»':'ç¼–è¾‘åˆ†ç±»'" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!-- <span>è¿™æ˜¯ä¸€æ®µä¿¡æ¯</span> -->
            <el-form :model="categoryDetail" ref="categoryDetail" label-width="120px" :rules="rules" @submit.native.prevent="">
                <el-form-item label="åˆ†ç±»åç§°" prop="name">
                    <el-input v-model="categoryDetail.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="åˆ†ç±»ç¼©ç•¥å›¾" prop="banner">
                    <span>(banneré•¿å®½è¦æ±‚ï¼š2278x516)</span>
                    <form id="minPicForm" method="post" enctype="multipart/form-data">
                        <!-- å•å›¾ç‰‡ä¸Šä¼  -->
                        <el-upload
                            class="avatar-uploader"
                            v-model="categoryDetail.banner"
                            action="'string'"
                            list-type="picture-card"
                            :auto-upload="false"
                            :show-file-list="false"
                            :on-change="handleCrop"
                        >
                            <!-- :http-request="upload" -->
                            <img 
                                v-if="categoryDetail.banner" 
                                :src="categoryDetail.banner" 
                                class="avatar" 
                                ref="singleImg" 
                                @mouseenter="mouseEnter" 
                                @mouseleave="mouseLeave" 
                                :style="{width:bannerWidth+'px',height:bannerHeight+'px'}" 
                            />
                            <i v-else class="el-icon-plus avatar-uploader-icon" :style="{width:bannerWidth+'px',height:bannerHeight+'px','line-height':bannerHeight+'px','font-size':bannerHeight/6+'px'}" ></i>
                            <div id="uploadIcon" v-if="categoryDetail.banner" ref="reupload" @mouseenter="mouseEnter" @mouseleave="mouseLeave" :style="{width:'100%'}" >
                                <i class="el-icon-zoom-in" title="æŸ¥çœ‹åŽŸå›¾" :style="{color:'#2E2E2E',fontSize:'25px',display:'inline-block',paddingRight:'15px'}" ></i>
                                <i class="el-icon-refresh-right" title="é‡æ–°ä¸Šä¼ " :style="{color:'#2E2E2E',fontSize:'25px',display:'inline-block'}" ></i>
                            </div>
                            <div class="reupload icon" ref="uploading" :style="{width:reuploadWidth+'px',height:reuploadWidth+'px','line-height':reuploadWidth+'px','font-size':reuploadWidth/5+'px'}" >ä¸Šä¼ ä¸­..</div>
                            <div class="reupload icon" ref="failUpload" :style="{width:reuploadWidth+'px',height:reuploadWidth+'px','line-height':reuploadWidth+'px','font-size':reuploadWidth/5+'px'}" >ä¸Šä¼ å¤±è´¥</div>
                        </el-upload>
                        <!-- <el-dialog :visible.sync="dialogVisible">
                            <img width="100%" :src="dialogImageUrl" alt />
                        </el-dialog> -->
                    </form>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">å– æ¶ˆ</el-button>
                <el-button type="primary" @click="commitHandle(handleType)">ç¡® å®š</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            handleType:"add",
            dialogVisible: false,
            bannerWidth:396,
            bannerHeight:88,
            file:'',
            reupload: true, // æŽ§åˆ¶"é‡æ–°ä¸Šä¼ "å¼€å…³
            categoryData: [],
            categoryDetail: {
                _id: "",
                name: "",
                banner: "",
                // addtime: "",
                // edittime: "",
            },
            rules:{
                name:[{ required: true, message: 'å†…å®¹ä¸èƒ½ä¸ºç©º', trigger: 'blur' }],
                banner:[{required:true,message:'è¯·ä¸Šä¼ åˆ†ç±»å›¾banner',trigger:'blur'}]
            }
        }
    },
    methods: {
        getData() {
            this.$axios({ url: '/admin/categories' })
                .then(res => {
                    this.categoryData = res.data.categories
                })
        },
        commitHandle(type){ // æ·»åŠ åˆ†ç±»æ“ä½œ
            if(this.categoryDetail.banner==''){
                this.$message({ type: 'info', message: 'è¯·æ·»åŠ å°é¢å›¾banner' });
                return false;
            }
            if(type=="add"){
                this.$axios({
                    url:"/admin/categories/add",
                    data:{
                        'name':this.categoryDetail.name,
                        'banner':this.categoryDetail.banner
                    },
                    method:'post'
                }).then(res=>{
                    console.log(res);
                    this.getData();
                    this.dialogVisible = false;
                });
            }
            else if(type=="edit"){
                this.$axios({
                    url: "/admin/categories/edit",
                    method: "post",
                    data: {
                        "id": this.categoryDetail._id,
                        "name": this.categoryDetail.name,
                        "banner": this.categoryDetail.banner,
                    }
                }).then(res => {
                    if (res.data.code == 1) {
                        this.$message({
                            type: 'success',
                            message: 'æ›´æ–°æˆåŠŸ!'
                        });
                    } else {
                        this.$message({
                            type: 'info',
                            message: 'æ›´æ–°å¤±è´¥!!'
                        });
                    }
                }).then(() => {
                    this.getData();
                    this.dialogVisible=false;
                })
            }
        },
        open(){
            this.dialogVisible=true;
            this.categoryDetail.name="";
            this.categoryDetail.banner="";
            this.handleType="add";
        },
        del(index, row) { // åˆ é™¤æŒ‰é’®
            this.$confirm('æ°¸ä¹…åˆ é™¤åˆ†ç±»"' + row.name + '", æ˜¯å¦ç»§ç»­?', 'æç¤º', {
                confirmButtonText: 'ç¡®å®š',
                cancelButtonText: 'å–æ¶ˆ',
                type: 'warning'
            }).then(() => {
                this.$axios({
                    url: '/admin/categories/del',
                    method: 'post',
                    data: {
                        "id": row._id
                    }
                }).then(res => {
                    if (res.data.code == 1) {
                        this.$message({
                            type: 'success',
                            message: 'åˆ é™¤æˆåŠŸ!'
                        });
                    } else {
                        this.$message({
                            type: 'info',
                            message: 'åˆ é™¤å¤±è´¥å•¦!'
                        });
                    }
                }).then(() => {
                    this.getData()
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: 'å·²å–æ¶ˆåˆ é™¤'
                });
            });
            // **************************
            //   this.$axios({
            //     url: '/admin/categories/del',
            //     method: 'post',
            //     data: {
            //       id: row._id
            //     }
            //   }).then(res => {
            //     if (res.data.code == 1) {
            //       this.$message({
            //         type: 'success',
            //         message: res.data.msg
            //       });
            //     }
            //   })
        },
        edit(index, row) { // ç¼–è¾‘æŒ‰é’®
            this.handleType="edit";
            this.dialogVisible=true;
            this.categoryDetail._id=row._id;
            this.categoryDetail.name=row.name;
            this.categoryDetail.banner=row.banner;
        },
        setIdColumn({ row, column, rowIndex }) { // è®¾ç½®æ ç›®æ ·å¼
            if (column.property == '_id') {
                return 'cell_nowrap'
            }
        },
        handleClose(done) { // å…³é—­å¼¹çª—
            this.$confirm('ç¡®è®¤å…³é—­ï¼Ÿ')
                .then(_ => {
                    done();
                })
                .catch(_ => { });
        },
        mouseEnter() {//é¼ æ ‡åˆ’å…¥æ˜¾ç¤ºâ€œé‡æ–°ä¸Šä¼ â€
            this.$refs.reupload.style.display = 'block'
            if (this.$refs.failUpload.style.display === 'block') {
                this.$refs.failUpload.style.display = 'none'
            }
            this.$refs.singleImg.style.opacity = '0.6'
        },
        mouseLeave() { // é¼ æ ‡åˆ’å‡ºéšè—â€œé‡æ–°ä¸Šä¼ â€
            this.$refs.reupload.style.display = 'none'
            this.$refs.singleImg.style.opacity = '1'
        },
        handleCrop(files, fileList) { // ç‚¹å‡»å¼¹å‡ºå‰ªè£æ¡†
            let tempForm = new FormData();
            tempForm.append('file',files.raw);
            this.$refs.uploading.style.display = 'block';
            this.$axios.post('/pic/upload',tempForm).then(res=>{
                this.$refs.uploading.style.display = 'none';
                if(res.status==200){
                    console.log(res);
                    this.categoryDetail.banner = res.data.imageUrl;
                }else {
                    // ä¸Šä¼ å¤±è´¥åˆ™æ˜¾ç¤ºä¸Šä¼ å¤±è´¥ï¼Œå¦‚å¤šå›¾åˆ™ä»Žå›¾ç‰‡åˆ—è¡¨åˆ é™¤å›¾ç‰‡
                    this.$refs.failUpload.style.display = 'block'
                }
            })
        },
    },
    created() {
        this.getData()
    },
}
</script>

<style lang="less" scoped>
#category {
    padding: 15px;
    .avatar {
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        width: 150px;
        height: 34px;
        // max-height: 34px;
        display: block;
        margin: 0 auto;
    }
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }

    .avatar-uploader .el-upload:hover {
        border-color: #409eff;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 150px;
        height: 34px;
        line-height: 34px;
        text-align: center;
    }
    // æ–°å¢žçš„ä¸Šä¼ ç»„ä»¶æ ·å¼
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409eff;
    }
    .avatar-uploader-icon {
        color: #8c939d;
        text-align: center;
    }
    .avatar {
        display: block;
    }
    .reupload {
        border-radius: 50%;
        position: absolute;
        color: #fff;
        background-color: #000000;
        opacity: 0.6;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: none;
    }
    #uploadIcon {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: none;
    }
    .avatar-uploader .el-upload.el-upload--picture-card {
        width: unset !important;
        height: unset !important;
    }
}

</style>


