<template>
    <div id="massage_list">
        <el-table :data="tableData" stripe style="width: 100%" :cell-class-name="setCell">
            <el-table-column prop="addtime" label="æ—¥æœŸ">
                <template slot-scope="scope">
                    <p>{{scope.row.addtime|date}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="viewer" label="ç”¨æˆ·/é‚®ç®±"></el-table-column>
            <el-table-column prop="massage" label="å†…å®¹"></el-table-column>
            <el-table-column label="æ“ä½œ">
                <template slot-scope="scope">
                    <el-button size="mini" @click="dialog=true; handleCheck(scope.$index, scope.row)" >æŸ¥çœ‹</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)" >åˆ é™¤</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- å¼¹çª—ç»„ä»¶ -->
        <el-dialog title="ç•™è¨€è¯¦æƒ…" :visible.sync="dialogTableVisible">
            <el-form :label-position="labelPosition" label-width="100px">
                <el-form-item label="æ—¶é—´">
                    <div v-html="fixedTime" class="chosen_content"></div>
                </el-form-item>
                <el-form-item label="ç”¨æˆ·/é‚®ç®±">
                    <el-input v-model="chosen.viewer" disabled class="disabled"></el-input>
                </el-form-item>
                <el-form-item label="ä¸»é¢˜">
                    <el-input v-model="chosen.subject" disabled class="disabled"></el-input>
                </el-form-item>
                <el-form-item label="å†…å®¹">
                    <div v-html="chosen.content" class="chosen_content"></div>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogTableVisible = false">ç¡® å®š</el-button>
            </span>
        </el-dialog>
        <!-- å¼¹çª—ç»„ä»¶ -->
        <el-divider></el-divider>
        <!-- åˆ†é¡µç»„ä»¶ -->
        <el-pagination
            background
            layout="prev, pager, next"
            :page-size="5"
            :page-count="pages"
            :total="total"
            @current-change="pageChange"
        ></el-pagination>
        <!-- åˆ†é¡µç»„ä»¶ -->
    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            curPage: 1,
            total: 1,
            pages: 1,
            dialogTableVisible: false, // æŸ¥çœ‹è¯¦æƒ…çš„å¼¹çª—é»˜è®¤ä¸æ˜¾ç¤º
            labelPosition: 'right',
            chosen: {
                viewer: '',
                addtime: '',
                subject: '',
                content: ''
            }
        }
    },
    mounted() {
        this.getMassageList()
    },
    methods: {
        handleCheck(index, row) {
            this.chosen.addtime = row.addtime;
            this.chosen.viewer = row.viewer;
            this.chosen.subject = row.subject;
            this.chosen.content = row.massage;
            this.dialogTableVisible = true;
        },
        handleDelete(index, row) {
            let ids = [];
            ids.push(row._id);
            this.$confirm('æ°¸ä¹…åˆ é™¤è¯¥æ¡ç•™è¨€å—, æ˜¯å¦ç»§ç»­?', 'æç¤º', {
                confirmButtonText: 'ç¡®å®š',
                cancelButtonText: 'å–æ¶ˆ',
                type: 'warning'
            }).then(() => {
                this.$axios({
                    url: "/admin/massage/del",
                    data: { ids: ids },
                    method: "post"
                }).then(res => {
                    if (res.data.code == 1) {
                        this.$message({ type: 'success', message: res.data.msg });
                    } else {
                        this.$message({ type: 'warning', message: res.data.msg });
                    }
                }).then(_ => this.getMassageList());
            });
        },
        setCell(row, column, rowIndex, columnIndex) { // è®¾ç½®ç±»åæ¥æ›´æ”¹æ ·å¼
            if (row.column.label === 'å†…å®¹') {
                return 'cell_nowrap'
            }
        },
        getMassageList() {
            this.$axios({
                url: "/admin/massage",
                method: "get",
                params: { page: this.curPage }
            }).then(res => {
                this.tableData = res.data.massages;
                this.total = res.data.total;
                this.pages = res.data.pages;
            })
        },
        pageChange(currentPage) {
            this.curPage = currentPage;
            this.getMassageList();
        }
    },
    computed: {
        fixedTime: {
            get(val) {
                let date = new Date(val.chosen.addtime);
                return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()} ${date.getHours() >= 10 ? date.getHours() : '0' + date.getHours()}:${date.getMinutes() >= 10 ? date.getMinutes() : '0' + date.getMinutes()}:${date.getSeconds() >= 10 ? date.getSeconds() : '0' + date.getSeconds()}`
            }
        }
    }
}
</script>

<style lang="less" scoped>
#massage_list {
    padding: 15px;
}
.chosen_content {
    border: 1px solid #e4e7ed;
    background-color: #f5f7fa;
    border-radius: 4px;
    padding: 0 15px;
}
</style>


