<template>
  <div>
    <div class="cropper-content">
      <!-- å‰ªè£æ¡† -->
      <div class="cropper">
        <vueCropper ref="cropper" :img="option.img" :outputSize="option.size" :outputType="option.outputType" :info="true" :full="option.full" :canMove="option.canMove" :canMoveBox="option.canMoveBox" :original="option.original" :autoCrop="option.autoCrop" :autoCropWidth="option.autoCropWidth" :autoCropHeight="option.autoCropHeight" :fixedBox="option.fixedBox" @realTime="realTime" :fixed="option.fixed" :fixedNumber="fixedNumber"></vueCropper>
      </div>
      <!-- é¢„è§ˆæ¡† -->
      <div class="show-preview" :style="{'width': '300px', 'height': '300px',  'overflow': 'hidden', 'margin': '0 25px', 'display':'flex', 'align-items' : 'center'}">
        <div :style="previews.div" class="preview">
          <img :src="previews.url" :style="previews.img">
        </div>
      </div>
    </div>
    <div class="footer-btn">
      <!-- ç¼©æ”¾æ—‹è½¬æŒ‰é’® -->
      <div class="scope-btn">
        <el-button type="primary" icon="el-icon-zoom-in" @click="changeScale(1)"></el-button>
        <el-button type="primary" icon="el-icon-zoom-out" @click="changeScale(-1)"></el-button>
        <el-button type="primary" @click="rotateLeft">é€†æ—¶é’ˆæ—‹è½¬</el-button>
        <el-button type="primary" @click="rotateRight">é¡ºæ—¶é’ˆæ—‹è½¬</el-button>
      </div>
      <!-- ç¡®è®¤ä¸Šä¼ æŒ‰é’® -->
      <div class="upload-btn">
        <el-button type="primary" @click="uploadImg('blob')">ä¸Šä¼ </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import VueCropper from 'vue-cropper'
export default {
  data () {
    return {
      previews: {}, // é¢„è§ˆæ•°æ®
      option: {
        img: '', // è£å‰ªå›¾ç‰‡çš„åœ°å€  (é»˜è®¤ï¼šç©º)
        size: 1, // è£å‰ªç”Ÿæˆå›¾ç‰‡çš„è´¨é‡  (é»˜è®¤:1)
        full: true, // æ˜¯å¦è¾“å‡ºåŽŸå›¾æ¯”ä¾‹çš„æˆªå›¾ é€‰trueç”Ÿæˆçš„å›¾ç‰‡ä¼šéžå¸¸å¤§  (é»˜è®¤:false)
        outputType: 'png', // è£å‰ªç”Ÿæˆå›¾ç‰‡çš„æ ¼å¼  (é»˜è®¤:jpg)
        canMove: true, // ä¸Šä¼ å›¾ç‰‡æ˜¯å¦å¯ä»¥ç§»åŠ¨  (é»˜è®¤:true)
        original: false, // ä¸Šä¼ å›¾ç‰‡æŒ‰ç…§åŽŸå§‹æ¯”ä¾‹æ¸²æŸ“  (é»˜è®¤:false)
        canMoveBox: true, // æˆªå›¾æ¡†èƒ½å¦æ‹–åŠ¨  (é»˜è®¤:true)
        autoCrop: true, // æ˜¯å¦é»˜è®¤ç”Ÿæˆæˆªå›¾æ¡†  (é»˜è®¤:false)
        autoCropWidth: 148, // é»˜è®¤ç”Ÿæˆæˆªå›¾æ¡†å®½åº¦  (é»˜è®¤:80%)
        autoCropHeight: 96, // é»˜è®¤ç”Ÿæˆæˆªå›¾æ¡†é«˜åº¦  (é»˜è®¤:80%)
        fixedBox: false, // å›ºå®šæˆªå›¾æ¡†å¤§å° ä¸å…è®¸æ”¹å˜  (é»˜è®¤:false)
        fixed: true, // æ˜¯å¦å¼€å¯æˆªå›¾æ¡†å®½é«˜å›ºå®šæ¯”ä¾‹  (é»˜è®¤:true)
        fixedNumber: [1.777777777 , 1] // æˆªå›¾æ¡†æ¯”ä¾‹  (é»˜è®¤:[1:1])
      },
      downImg: '#'
    }
  },
  props: ['imgFile', 'fixedNumber'],
  methods: {
    changeScale (num) {
      // å›¾ç‰‡ç¼©æ”¾
      num = num || 1
      this.$refs.cropper.changeScale(num)
    },
    rotateLeft () {
      // å‘å·¦æ—‹è½¬
      this.$refs.cropper.rotateLeft()
    },
    rotateRight () {
      // å‘å³æ—‹è½¬
      this.$refs.cropper.rotateRight()
    },
    Update () {
      this.option.img = this.imgFile.url
    },
    realTime (data) {
      // å®žæ—¶é¢„è§ˆ
      this.previews = data
    },
    uploadImg (type) {
      // å°†å‰ªè£å¥½çš„å›¾ç‰‡å›žä¼ ç»™çˆ¶ç»„ä»¶
      event.preventDefault()
      let that = this
      if (type === 'blob') {
        this.$refs.cropper.getCropBlob(data => {
          that.$emit('upload', data)
        })
      } else {
        this.$refs.cropper.getCropData(data => {
          that.$emit('upload', data)
        })
      }
    }
  },
  components: { VueCropper }
}
</script>
<style>
.cropper-content {
  display: flex;
  display: -webkit-flex;
  justify-content: flex-end;
  -webkit-justify-content: flex-end;
}
.cropper-content .cropper {
  width: 350px;
  height: 300px;
}
.cropper-content .show-preview {
  flex: 1;
  -webkit-flex: 1;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  -webkit-justify-content: center;
  overflow: hidden;
  border: 1px solid #cccccc;
  background: rgba(0, 0, 0, .8);
  margin-left: 40px;
}
.preview {
  overflow: hidden;
  border: 1px dashed #000;
  background: rgba(255, 255, 255, .9);
}
.footer-btn {
  margin-top: 30px;
  display: flex;
  display: -webkit-flex;
  justify-content: flex-end;
  -webkit-justify-content: flex-end;
}
.footer-btn .scope-btn {
  /* width: 250px; */
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
  -webkit-justify-content: space-between;
}
.footer-btn .upload-btn {
  flex: 1;
  -webkit-flex: 1;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  -webkit-justify-content: center;
}
.footer-btn .btn {
  outline: none;
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  -webkit-appearance: none;
  text-align: center;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  -webkit-transition: 0.1s;
  transition: 0.1s;
  font-weight: 500;
  padding: 8px 15px;
  font-size: 12px;
  border-radius: 3px;
  color: #fff;
  background-color: #67c23a;
  border-color: #67c23a;
}
</style>



