                    this.getArticles()
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: 'å·²å–æ¶ˆåˆ é™¤'
                });
            });

        },
        handleEdit(index, row) { // ç¼–è¾‘æ–‡ç« 
            this.minpic_url_list = [] // å…ˆæ¸…ç©º
            let id = row._id
            let nowForm = {}
            this.originArticleData.contents.map(v => {
                if (v._id == id) {
                    nowForm = v
                }
            });
            nowForm.category = row.category
            nowForm.content = nowForm.composition
            nowForm.id = id
            this.categoryData.map(v => { if (v.name == nowForm.category) nowForm.category = v._id })
            this.form = nowForm
            this.dialogType = 'edit'
            if (nowForm.minpic_url != "") {
                this.minpic_url_list.push({ url: nowForm.minpic_url })
                this.imageUrl = nowForm.minpic_url
                this.form.minpic_url = nowForm.minpic_url
            } else {
                this.hideUpload = false
            }
            this.isShow = nowForm.isShow + "" || "0"
        },
        /* ********* å¯Œæ–‡æœ¬ç¼–è¾‘å™¨å›¾ç‰‡ä¸Šä¼ æ“ä½œ *********** */
        selectImg() {  //é€‰æ‹©å›¾ç‰‡
            document.getElementById("selectImg").click();
        },
        inputChangeImg: function () { // input é€‰æ‹©å›¾ç‰‡æ—¶çš„æ“ä½œ
            let that = this;
            let input = document.getElementById('selectImg');
            if (input.files && input.files[0]) {
                let item = input.files[0];
                let reader = new FileReader();
                reader.readAsDataURL(item);
                reader.onload = function () {
                    let url = this.result;
                    that.uploadPic(item).then(res => {
                        that.handleSuccess(res.data)
                    })
                }
            }
        },
        handleSuccess(res) {
            let quill = this.$refs.myQuillEditor.quill
            let length = quill.getSelection().index;   Â // èŽ·å–å…‰æ ‡æ‰€åœ¨ä½ç½®
            quill.insertEmbed(length, 'image', res.imageUrl);  Â // æ’å…¥å›¾ç‰‡ Â res.urlä¸ºæœåŠ¡å™¨è¿”å›žçš„å›¾ç‰‡åœ°å€  
            quill.setSelection(length + 1);              // è°ƒæ•´å…‰æ ‡åˆ°æœ€åŽ
        },
        uploadPic: function (item) {  //æäº¤å›¾ç‰‡
            let form = document.getElementById('upload'),
                formData = new FormData(form);
            formData.file = item;
            //   return this.$axios({ url: "/admin/content/img_upload", method: "post", data: formData }) // è°ƒç”¨æŽ¥å£ä¸Šä¼ å›¾ç‰‡
            return this.$axios({ url: "/pic/upload", method: "post", data: formData }) // è°ƒç”¨æŽ¥å£ä¸Šä¼ å›¾ç‰‡
        },
        /* ********* å¯Œæ–‡æœ¬ç¼–è¾‘å™¨å›¾ç‰‡ä¸Šä¼ æ“ä½œ *********** */

        /* ************* cropperæˆªå›¾ä¸Šä¼  ************** */
        handlePreviewSingle(file) {//ç‚¹å‡»è¿›è¡Œå›¾ç‰‡å±•ç¤º
            console.log('aaaaa',this.dialogImageUrl);
            console.log(this.imageUrl);
            this.dialogImageUrl = this.imageUrl
            this.dialogVisible = true
        },
        mouseEnter() {//é¼ æ ‡åˆ’å…¥æ˜¾ç¤ºâ€œé‡æ–°ä¸Šä¼ â€
            this.$refs.reupload.style.display = 'block'
            if (this.$refs.failUpload.style.display === 'block') {
                this.$refs.failUpload.style.display = 'none'
            }
            this.$refs.singleImg.style.opacity = '0.6'
        },
        mouseLeave() {
            // é¼ æ ‡åˆ’å‡ºéšè—â€œé‡æ–°ä¸Šä¼ â€
            this.$refs.reupload.style.display = 'none'
            this.$refs.singleImg.style.opacity = '1'
        },
        handleCrop(files, fileList) {
            // ç‚¹å‡»å¼¹å‡ºå‰ªè£æ¡†
            this.cropperModel = true
            this.file = files
            //  this.imageUrl = file.url
        },
        upload(data) {
            // è‡ªå®šä¹‰uploadäº‹ä»¶
            this.$refs.uploading.style.display = 'block'
            let imgData = new FormData();
            imgData.append('file', data);
            imgData.image = data;
            this.$axios.post(this.targetUrl, imgData).then(res => {
                // ä¸Šä¼ å®ŒæˆåŽéšè—æ­£åœ¨ä¸Šä¼ 
                this.$refs.uploading.style.display = 'none'
                if (res.status === 200) {
                    // ä¸Šä¼ æˆåŠŸå°†ç…§ç‰‡ä¼ å›žçˆ¶ç»„ä»¶
                    const currentPic = res.data.imageUrl
                    this.$emit('imgupload', currentPic)
                    this.imageUrl = currentPic
                    this.form.minpic_url = currentPic
                    console.log('ä¸Šä¼ æˆåŠŸ,urlä¸º ', this.imageUrl)
                } else {
                    // ä¸Šä¼ å¤±è´¥åˆ™æ˜¾ç¤ºä¸Šä¼ å¤±è´¥ï¼Œå¦‚å¤šå›¾åˆ™ä»Žå›¾ç‰‡åˆ—è¡¨åˆ é™¤å›¾ç‰‡
                    this.$refs.failUpload.style.display = 'block'
                }
            })
            this.cropperModel = false
        },
        formatImgArr(arr) {
            const result = arr.map((item, index) => {
                if (typeof item === 'string') {
                    return {
                        url: item,
                        uid: `index${index}`
                    }
                } else {
                    return item.url
                }
            })
            return result
        },
        beforeClose(done) {
            this.uploadList.pop()
            this.cropperModel = false
        },
        /* ************* cropperæˆªå›¾ä¸Šä¼  ************** */
        isVideo(val) {
            let temp = null;
            this.categoryData.forEach(v => { if (v.name == val) temp = v._id; });
            return temp;
        }
    }
}
</script>

